import { useRouter } from "next/router";
import { AuthContext } from "../../context/AuthContext";
import { useEffect, useContext, useRef, useState } from "react";
import Peer from "peerjs";

const MeetingPage = () => {
  const auth = useContext(AuthContext);
  const user = auth?.user;

  const router = useRouter();
  const { id: meetingId } = router.query;
  const [peer, setPeer] = useState<Peer | null>(null);
  const [socket, setSocket] = useState<WebSocket | null>(null);
  const [localStream, setLocalStream] = useState<MediaStream | null>(null);
  const myVideoRef = useRef<HTMLVideoElement>(null);
  const [messages, setMessages] = useState<{ sender: string; message: string }[]>([]);
  const [newMessage, setNewMessage] = useState("");

  useEffect(() => {
    if (!meetingId || !user) return;

    const websocketURL = process.env.NEXT_PUBLIC_WEBSOCKET_URL || "ws://localhost:8000";
    const newSocket = new WebSocket(`${websocketURL}/ws/${meetingId}/${user.username}`);

    newSocket.onopen = () => {
      console.log("âœ… WebSocket Connected!");
    };

    newSocket.onmessage = (event) => {
      const data = JSON.parse(event.data);
      if (data.type === "chat-message") {
        setMessages((prev) => [...prev, { sender: data.sender, message: data.message }]);
      }
    };

    setSocket(newSocket);

    return () => {
      newSocket.close();
    };
  }, [meetingId, user]);

  const sendMessage = () => {
    if (!newMessage || !socket) return;
    socket.send(JSON.stringify({ type: "chat-message", sender: user.username, message: newMessage }));
    setNewMessage("");
  };

  return (
    <div className="flex h-screen">
      {/* Video Call Section */}
      <div className="flex-1 flex items-center justify-center">
        <video ref={myVideoRef} autoPlay muted className="w-64 h-48 border rounded-lg" />
      </div>

      {/* Chat Section */}
      <div className="w-1/3 border-l bg-gray-100 p-4">
        <h2 className="text-lg font-bold mb-2">Live Chat</h2>
        <div className="overflow-y-auto h-80 p-2 border bg-white">
          {messages.map((msg, index) => (
            <div key={index} className={`p-1 my-1 ${msg.sender === user.username ? "text-right" : "text-left"}`}>
              <span className="font-bold">{msg.sender}: </span>
              {msg.message}
            </div>
          ))}
        </div>
        <div className="flex mt-2">
          <input
            type="text"
            className="border p-2 flex-1 rounded-lg"
            placeholder="Type a message..."
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
          />
          <button className="bg-blue-500 text-white px-4 py-2 ml-2 rounded-lg" onClick={sendMessage}>
            Send
          </button>
        </div>
      </div>
    </div>
  );
};

export default MeetingPage;
